import { reactive } from 'vue'
import type { FormRules } from 'element-plus'

export const rules = reactive<FormRules>({
  username: [
    { required: true, message: '', trigger: 'blur' },
    {
      pattern: /^[a-z0-9]{5,10}$/,
      message: '510'
    }
  ],
  password: [
    { required: true, message: '', trigger: 'blur' },
    {
      pattern: /^[a-z0-9]{3,}$/,
      message: '3',
      trigger: 'blur'
    }
  ]
})
